@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap');

#logoHeader{
    height: 5vw;
    position: absolute;
    top: 1vw;
    left: 1vw;
    cursor: pointer;
}
.btnDrop{
    position:absolute;
    background-color: transparent;
    color:white;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
    height: 3vw;

    font-size: 1.1vw;
    top: 1.5vw;
}

#btnPropos{
    left: 8.5vw;
}

#btnInfos{
    left: 18vw;
}


#btnBilleterie{
    background-color: #ffA06A;
    border-radius: 2vw;
    color: white;
    cursor: pointer;
    font-weight: bold;
    padding: 0.5vw 1vw;
    text-align: center;
    transition: 200ms;
    width: 12vw;
    box-sizing: border-box;
    border: 0;
    font-size: 1.25vw;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;

    position: absolute;
    right: 2vw;
    top:2vw;
    cursor: pointer;
}
#header{
    position:absolute;
    height: 10vw;
    width: 100%;
    background-color: transparent;
}
.btnDrop{
    transition: 200ms;
    display:block;
    width:13.5vw;
    height:10vw;
}
.btnDrop:hover{
    transition:200ms;
    top:0px;
}
.btnDrop .rotator {
    transition: 200ms;
    opacity: 1;
}
.btnDrop:hover .rotator {
    transition: 200ms;
    opacity: 0;
}

#header p{
    font-family: 'Roboto Condensed', sans-serif;
  font-weight:400;
    font-size:1.25vw;
}


#header .dropContent a {
    font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300;
    font-size:1vw;
	width:7.5vw;
    display:block;
    margin: 0px;
    margin-left: 0.5vw;
    padding-bottom:0.5vw;
    border:0px;

    text-decoration: none;
    color:white;
    
}

.dropContent{
    display: block;
    opacity:0;
    position: absolute;
    width:7.5vw;
    transition:500ms;
    margin-left:2.5vw;

    
    
}
#header div:hover .dropContent {
    transition:500ms;
	display:block;
    opacity:1;
    border:0px;
    border-left: 0.1vw;
    border-style: solid;  
}
#header .btnDrop p{
    margin-bottom:0.5vw;
}