@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;700&display=swap');

#imgHeaderContact{
    display: block ;
    float:left;
    top:0px;

    height:7vw;
    width:100%;
    background-color: #535353;
}
#headerContact{
    height:7vw;
    display: relative;
}
#contact{
    position: relative;
    float:left;
    width: 100%;
}
p{
    font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300;
    font-size:1vw;
    margin-left: 2.5vw;
    margin-right: 2.5vw;

}
#contactTitle{
    font-weight:700;
    font-size:2vw;
    margin-left: 0px;
    margin-right: 0px;
}
.contactSubtitle{
    font-weight:700;
    margin-left: 1.5vw;
    margin-right: 1.5vw;
}
#contentContact{
    padding-left:2.5vw;
    padding-right:2.5vw;
    padding-bottom:25vw;   
}